body {
  text-align: center;
}

table {
  margin: 6vh auto;
  border-collapse: collapse;
}

th {
  /*font-weight: normal;*/
  padding: 1ex;
}

td {
  user-select: none;
  border: 1px solid;
  width: 4ex;
  height: 4ex;
  text-align: center;
  background: #0000;
  transition: background .15s;
}

td.\31 {
  background: #0f05;
  transition: none;
}

td.-\31 {
  background: #f005;
  transition: none;
}